import React, { Component } from "react";
import "./App.css";
import Models from "./Components/Models";
const animals = require("./3dmodelDB/animals.json");
const foods = require("./3dmodelDB/foods.json");
const furnitures = require("./3dmodelDB/furnitures.json");
const natures = require("./3dmodelDB/natures.json");
const technologies = require("./3dmodelDB/technologies.json");
const transports = require("./3dmodelDB/transports.json");
const arr = [natures, technologies, transports];
class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      models: [animals, foods, furnitures],
      count: 0
    };
  }
  handleScroll = () => {
    if (
      window.innerHeight + window.pageYOffset >=
      document.body.scrollHeight + 23
    ) {
      let newModel = [...this.state.models, arr[this.state.count]];
      let newCount = this.state.count;
      this.setState({
        models: newModel,
        count: ++newCount
      });
    }
    if (this.state.count > 2)
      window.removeEventListener("scroll", this.handleScroll);
  };
  componentDidMount() {
    window.addEventListener("scroll", this.handleScroll);
  }
  componentWillUnmount() {
    window.removeEventListener("scroll", this.handleScroll);
  }
  render() {
    return (
      <div className="container mt-4">
        <h2>3D Models</h2>
        <Models models={this.state.models} />
      </div>
    );
  }
}

export default App;
