import React, { Component } from "react";
import Model from "./model/Model";

class Models extends Component {
  render() {
    let count = 0;
    return (
      <div>
        {this.props.models.map(result => {
          return (
            <div className="heading" key={++count}>
              <h4>{result[0].category}</h4>
              <Model model={result} />
            </div>
          );
        })}
      </div>
    );
  }
}

export default Models;
